/* eslint-disable default-case */
import { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import './Registration.css';

import eyeOff from 'assets/icons/eye_crossed.svg';
import eyeOpen from 'assets/icons/eye.svg';

import ModalBack from '../ModalBack/ModalBack';

const Registration = (props) => {
  const navigate = useNavigate();

  const [nameInput, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const [nameDirty, setNameDirty] = useState(false);
  const [emailDirty, setEmailDirty] = useState(false);
  const [passwordDirty, setPasswordDirty] = useState(false);

  const [passwordShown, setPasswordShown] = useState(false);
  const [isErrorResponse, setIsErrorResponse] = useState(false);

  const togglePassword = () => {
    setPasswordShown(!passwordShown);
  };

  const [nameEmptyError, setNameEmptyError] = useState(
    'Please enter your Name'
  );
  const [emailEmptyError, setEmailEmptyError] = useState(
    'Please enter your Email'
  );

  const nameHandler = (e) => {
    setName(e.target.value);
    if (e.target.value.length < 1 || e.target.value.length > 25) {
      setNameEmptyError('The Name doesnt match required criteria');
    } else {
      setNameEmptyError('');
    }
  };
  const emailHandler = (e) => {
    setEmail(e.target.value);
    const regul =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (!regul.test(String(e.target.value).toLowerCase())) {
      setEmailEmptyError('The Email doesnt match required criteria');
    } else {
      setEmailEmptyError('');
    }
  };

  const blurHandler = (e) => {
    switch (e.target.name) {
      case 'name':
        setNameDirty(true);
        break;
      case 'email':
        setEmailDirty(true);
        break;
      case 'password':
        setPasswordDirty(true);
        break;
    }
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    console.log(nameInput, email, password);

    navigate('/login');
  };

  const ErrorPasswMsg = () => {
    return (
      <>
        {password.length === 0 && passwordDirty ? (
          <p className='error-message error-message_margin'>
            {'Please enter your Password'}
          </p>
        ) : (password.length > 0 && password.length < 6) ||
          password.length > 25 ? (
          <p className='error-message error-message_margin'>
            {'The password doesnt match required criteria'}
          </p>
        ) : (
          <p className='text error-message_margin'>
            {'Must be between 6 and 25 characters long.'}
          </p>
        )}
      </>
    );
  };

  return (
    <ModalBack>
      <div className='login__head-wrap'>
        <h2 className='title'>Welcome</h2>
        <p
          className={
            isErrorResponse
              ? 'error-message error-message_margin'
              : 'error-message error-message_margin error-message_hidden'
          }
        >
          This email already exists.
          {<ResponseError />}
          to reset password
        </p>
      </div>
      <form className='form' onSubmit={onSubmit}>
        <div className='form__input-wrap'>
          <input
            onBlur={(e) => blurHandler(e)}
            name='name'
            type='text'
            className='form__input'
            placeholder='Name'
            value={nameInput}
            onChange={(e) => {
              nameHandler(e);
            }}
            required
          />
          <p
            className={
              nameDirty && nameEmptyError
                ? 'error-message error-message_margin'
                : 'error-message error-message_margin error-message_hidden'
            }
          >
            {nameEmptyError}
          </p>
        </div>
        <div className='form__input-wrap'>
          <input
            onBlur={(e) => blurHandler(e)}
            name='email'
            type='text'
            className='form__input'
            placeholder='Email address'
            value={email}
            onChange={(e) => {
              emailHandler(e);
            }}
            required
          />
          <p
            className={
              emailDirty && emailEmptyError
                ? 'error-message error-message_margin'
                : 'error-message error-message_margin error-message_hidden'
            }
          >
            {emailEmptyError}
          </p>
        </div>
        <div className='form__input-wrap'>
          <div className='form__input-container'>
            <input
              onBlur={blurHandler}
              name='password'
              className='form__input form__input-passw '
              type={passwordShown ? 'text' : 'password'}
              placeholder='Password'
              value={password}
              onChange={(e) => {
                setPassword(e.target.value);
              }}
              required
            />
            <img
              onClick={togglePassword}
              className='form__eye'
              src={passwordShown ? eyeOpen : eyeOff}
              alt='eye icon'
            />
          </div>
          {<ErrorPasswMsg />}
        </div>
        <button
          type='submit'
          className='btn register__btn'
          disabled={
            nameInput &&
            email &&
            password.length > 5 &&
            password.length <= 25 &&
            !nameEmptyError &&
            !emailEmptyError
              ? false
              : true
          }
        >
          Confirm
        </button>
      </form>

      <p className='text register__text'>
        Already registered?{' '}
        <Link to={'/login'} className='link__green'>
          Log In
        </Link>
      </p>

      <p className='text terms-text'>
        By creating an account, I accept (Name page) Terms and Privacy statement
      </p>
    </ModalBack>
  );
};

const ResponseError = () => {
  return (
    <Link to={'/forgot-password'} className='link__forgot'>
      Click here
    </Link>
  );
};
export default Registration;
